.App {
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: lightgray;
}

.App-header {
  background-color: #282c34;
  height: 100px;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-header h1 {
  margin: 0;
}
.inputBar {
  margin-bottom: 0.5em;
}
article {
  margin: 1em 0;
  padding-bottom: 5px;
  border-bottom: 1px solid #282c34;
  /* border: 1px solid blue; */
}
article p {
  margin: 0;
}
.todoHolder {
  /* background-color: white; */
  position: fixed;
  overflow: scroll;
  top: 100px;
  right: 0;
  left: 0;
  bottom: 0;
  margin: 0 auto;
  max-width: 75vw;
  border-left: 3px solid;
  border-right: 3px solid;
  border-color: #282c34;
}
